* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
img {
  display: block;
  width: 100%;
  height: auto;
}
.friendList {
  max-width: 450px;
  margin: 0 auto;
}
.item {
  display: flex;
  justify-content: left;
  align-items: center;
  max-width: 450px;
  height: 150px;
  margin: 0 auto;
  padding: 10px;
  border: solid 1px black;
  border-radius: 6px;
}
.statusOn {
  display: block;
  height: 30px;
  width: 30px;
  margin-right: 20px;
  border: solid 1px black;
  border-radius: 60px;
  background-color: green;
}
.statusOf {
  composes: statusOn;
  background-color: red;
}
.avatar {
  width: 120px;
  height: 120px;
  border-radius: 30px;
  margin-right: 20px;
}
.name {
  font-size: 30px;
  font-weight: bold;
}
